.post {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.media {
  width: 400px;
  height: 400px;
  object-fit: cover;
}

.post-details {
  width: 400px;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 5px 10px;
}

.user-image {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.details {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-left: 10px;
}

.uploader-name {
  font-size: 1rem;
  font-weight: 500;
  color: #1f1f1f;
}

.time-posted {
  font-size: 0.8rem;
  font-weight: 400;
  color: #707070;
}

.actions {
  display: flex;
  align-items: center;
}

.comment-button {
  background: none;
  border: none;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-left: 15px;
}
